import React from 'react'
import axios from 'axios'
import config from '../../../utils/config'
const HOST = config.HOST


class MyInfo extends React.Component {

  componentWillMount() {
    // 请求个人资料
    axios.get(`${HOST}/api/users/user_info`)
      .then(res => {
        console.log(res)
        // updateUserInfoAction
      })
      .catch(err => {
        console.log(err)
      })
  }

  componentDidMount() {
    document.title = '优选 - 个人资料'
  }

  formSubmit(e) {
    e.preventDefailt();

  }

  render() {
    return (
      <div>
        <div>基本资料</div>
        <form name="baseInfoForm" method="post" onSubmit={this.formSubmit.bind(this)}>
          <div>亲爱的xxx，填写真实的资料，有助于好友找到你哦。</div>
          <div>
            <div>当前头像：</div>
            <div><img src={`${HOST}/image/member/avatar/default.jpg`} alt="当前头像"/></div>
          </div>
          {/*
          <div>
            <div>昵称：</div>
            <div><input type="text"/></div>
          </div>*/}
          <div>
            <div>真实姓名：</div>
            <input type="text"/>
          </div>
          <div>
            <div>性别</div>
            <div>
              <label>
                <input type="radio"/>
                男
              </label>
              <label>
                <input type="radio"/>
                女
              </label>
              <label>
                <input type="radio"/>
                保密
              </label>
            </div>
          </div>
          <div>
            <div>生日</div>
            <div>
              <select defaultValue="0" name="birth_year" id="birth_year">
                <option value="0">年</option>
                <option defaultValue="1940">1940</option>
              </select>
              <select defaultValue="0" name="birth_year" id="birth_year">
                <option value="0">月</option>
                <option defaultValue="2">2</option>
              </select>
              <select defaultValue="0" name="birth_year" id="birth_year">
                <option value="0">日</option>
                <option defaultValue="1">1</option>
              </select>
            </div>
          </div>
        </form>
      </div>
    )
  }
}
export default MyInfo
